<template>
    <div>
        <el-input
            type="textarea"
            :rows="10"
            placeholder="请输入内容"
            v-model="content">
        </el-input>
        <button v-on:click="submit">提交</button>
    </div>
</template>

<script>
export default {
    name: 'BlogWrite',
    data() {
        return {
            textarea: ''
        }
    },
    methods:{
        submit () {
                  this.$axios
        .post('/article', {
          content: this.content,
        })
        }
    }
}
</script>


