import Vue from 'vue'
import Router from 'vue-router'
import BlogLogin from '@/components/manage/BlogLogin.vue'
import GroupSpace from '@/components/home/GroupSpace.vue'
import BlogWrite from '@/components/blogoperation/BlogWrite.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/groupspace',
      name: 'GroupSpace',
      component: GroupSpace
    },
    {
      path: '/manage',
      redirect: '/login'
    },
    {
      path: '/write',
      name: 'BlogWrite',
      component: BlogWrite
    },
    {
      path: '/login',
      name: 'BlogLogin',
      component: BlogLogin
    }
  ]
})


